import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { DataTableResource } from 'angular7-data-table';

import { LeadersService } from './leaders.service';
import { GroupsService } from '../groups/groups.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

@Component({
  selector: 'app-leaders',
  templateUrl: './leaders.component.html',
  styleUrls: ['./leaders.component.scss'],
})
export class LeadersComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  constructor(
    private modalService: BsModalService,
    private toastr: ToastrService,
    private leadersService: LeadersService,
    private groupsService: GroupsService
  ) {}

  ngOnInit(): void {
    this.leadersService.getAll();
    this.leadersService.leaders.subscribe(async (data) => {
      if (data) {
        this.items = data;
        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter(
        (item) =>
          item.name.toLowerCase().includes(FilterRecord) ||
          item.leaderId?.group.name.toLowerCase().includes(FilterRecord)
      );
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }

  // Start select dropdown
  selectGroupsList: InputSelectItem[];
  selectedGroupId: string;
  getGroupsList() {
    this.groupsService.getAll();

    this.groupsService.groups.subscribe((data) => {
      if (data) {
        this.selectGroupsList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: ``,
          };
        });
      }
    });
  }
  selectGroup(event) {
    if (event) {
      this.selectedGroupId = event._id;
    }
  }
  // End select dropdown

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  addLeader() {
    this.modalRef.hide();
    this.leadersService.addLeader(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.leadersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  deleteLeader() {
    this.modalRef.hide();
    this.leadersService.deleteLeader(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.leadersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  addLeaderToGroup() {
    this.modalRef.hide();
    this.leadersService
      .addLeaderToGroup(this.itemId, this.selectedGroupId)
      .subscribe(
        (res) => {
          console.log(res);
          this.toastr.info(res['message'], 'Info');
          this.items = [];

          this.leadersService.getAll();
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
  }

  lockElement() {
    this.modalRef.hide();
    this.leadersService.lockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.leadersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  unlockElement() {
    this.modalRef.hide();
    this.leadersService.unlockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.leadersService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
