import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class LeadersService {
  constructor(private http: HttpClient) {}

  leaders = new BehaviorSubject(null);

  getAll() {
    return this.http.get(baseUrl + '/leader').subscribe((res) => {
      this.leaders.next(res['data']);
    });
  }

  addLeader(leaderId) {
    return this.http.post(baseUrl + `/leader/addLeader/${leaderId}`, {});
  }

  deleteLeader(leaderId) {
    return this.http.delete(baseUrl + `/leader/deleteLeader/${leaderId}`);
  }

  addLeaderToGroup(userId, groupId) {
    return this.http.post(
      baseUrl + `/leader/addLeaderToGroup/${userId}/${groupId}`,
      {}
    );
  }

  lockOne(itemId) {
    return this.http.get(baseUrl + `/leader/lock/${itemId}`);
  }

  unlockOne(itemId) {
    return this.http.get(baseUrl + `/leader/unlock/${itemId}`);
  }
}
